	module.exports = {"data":{"markdownRemark":{"html":"<p>You can receive payments on HackerOne for finding and reporting valid vulnerabilities in the form of:</p>\n<ul>\n<li>Bounties</li>\n<li>Swag</li>\n<li>Bonuses</li>\n</ul>\n<h3>Bounties</h3>\n<p>A bounty is money you get rewarded with for reported and resolved bugs. They're used to attract the best hackers and to keep them incentivized to hack their programs. Bounties are used to encourage you to focus on particular assets by altering the reward amount for different vulnerability types. You won't get rewarded for every report you send in, but only for useful, valid reports.</p>\n<p>After a program has decided to award you a bounty and the bounty has been awarded, you'll receive an email to claim the bounty. HackerOne will ask to collect your tax form before processing the payout.</p>\n<h4>Donating Bounties to Charity</h4>\n<p>HackerOne enables you to donate your bounty directly to charity.</p>\n<p>If you want to donate a bounty:</p>\n<ol>\n<li>Sign in to your HackerOne account and <a href=\"https://support.hackerone.com/hc/en-us/requests/new\">submit a support ticket</a>.</li>\n<li>\n<p>Mention the following in your request:</p>\n<ul>\n<li>The ID of the report you want to donate the bounty of. You can get the report ID from the URL (e.g. <a href=\"https://hackerone.com/reports/123\">https://hackerone.com/reports/123</a>, where 123 is the ID).</li>\n<li>The name and website of the charity you want to donate to.</li>\n<li>Whether or not you want to be named as the person donating or prefer to remain anonymous. Keep in mind that we can only attribute the donation to you if the charity allows us to give a name. </li>\n</ul>\n</li>\n</ol>\n<p>Upon receiving your request, HackerOne will do a quick check to see if a donation can be made to your selected charity. If for some reason a donation can't be made, HackerOne will notify you so an alternative can be made. </p>\n<p>Generally, if the charity accepts donations in USD through PayPal or Credit Card, HackerOne should be able to fulfill your request quickly. If the charity provides an electronic receipt, HackerOne will forward this to you as proof of payment.</p>\n<p>Keep in mind that you still need to have a valid tax form on file in order to donate your bounty. At this time, we can only donate bounties in full as we can't do partial or split donations.</p>\n<h3>Swag</h3>\n<p>Your program can elect to award you with various swag in addition to or instead of bounties. Swag includes merchandise as well as free coupons or vouchers for the services or products the program offers. Some programs also offer to host hackers near their office or cover admissions fees to conferences you're invited to attend.</p>\n<p>The program is responsible for fulfillment and delivery of swag to you.</p>\n<h3>Bonuses</h3>\n<p>Programs can award bonuses to recognize hackers for positive actions beyond finding valid vulnerabilities. Bonuses enable programs to offer more flexible incentives without increasing the market rate for bounties. </p>\n<h3>Tax Form</h3>\n<p>You must sign a tax form in order to receive payments. </p>\n<p>To sign your tax form:</p>\n<ol>\n<li>Go to your profile's <b>Settings > Payments</b>.</li>\n<li>Select <b>click here</b> on the green banner.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-028e1.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 47.161066048667436%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"tax-form-4\"\n        title=\"\"\n        src=\"/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-e7ffa.png\"\n        srcset=\"/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-49813.png 125w,\n/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-dc8a5.png 250w,\n/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-e7ffa.png 500w,\n/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-dda24.png 750w,\n/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-c5f18.png 1000w,\n/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-22484.png 1500w,\n/static/tax-form-4-181bd886a1fc51b3f39d0364896066de-028e1.png 1726w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"3\">\n<li>Fill out the fields on the <b>Sign tax form</b> window that pops open.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/tax-form-1-4071a1fb133ec401c19e3d82166ee896-34380.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 459px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 74.94553376906319%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"tax-form-1\"\n        title=\"\"\n        src=\"/static/tax-form-1-4071a1fb133ec401c19e3d82166ee896-34380.png\"\n        srcset=\"/static/tax-form-1-4071a1fb133ec401c19e3d82166ee896-2aca7.png 125w,\n/static/tax-form-1-4071a1fb133ec401c19e3d82166ee896-bdf37.png 250w,\n/static/tax-form-1-4071a1fb133ec401c19e3d82166ee896-34380.png 459w\"\n        sizes=\"(max-width: 459px) 100vw, 459px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n<li>Click <b>Save</b>. </li>\n<li>You'll be redirected to the Payments homepage where you can click the link in the yellow banner to sign your tax forms.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/tax-form-2-6f5d0ad214b4088f11c3cb0e2675f4bc-853ed.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 7.565789473684211%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAYAAABYBvyLAAAACXBIWXMAAAsSAAALEgHS3X78AAAAXUlEQVQI133NNw6AMBQD0Nz/aiBKiEKdaWmkDEyYr+wwPMkeLDOvOYLpEEnImcPrlnKL6AYEOyK5PvfkBJ57+cWus4Y9yF7BbGWm1gKaRCthlaQDkXtQDY1mMn16ARHplRzeyFwtAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"tax-form-2\"\n        title=\"\"\n        src=\"/static/tax-form-2-6f5d0ad214b4088f11c3cb0e2675f4bc-e7ffa.png\"\n        srcset=\"/static/tax-form-2-6f5d0ad214b4088f11c3cb0e2675f4bc-49813.png 125w,\n/static/tax-form-2-6f5d0ad214b4088f11c3cb0e2675f4bc-dc8a5.png 250w,\n/static/tax-form-2-6f5d0ad214b4088f11c3cb0e2675f4bc-e7ffa.png 500w,\n/static/tax-form-2-6f5d0ad214b4088f11c3cb0e2675f4bc-853ed.png 608w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/tax-form-3-2bb5571db7e3f6374656b1f677d6f952-16d6d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 102.7867095391211%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"tax-form-3\"\n        title=\"\"\n        src=\"/static/tax-form-3-2bb5571db7e3f6374656b1f677d6f952-e7ffa.png\"\n        srcset=\"/static/tax-form-3-2bb5571db7e3f6374656b1f677d6f952-49813.png 125w,\n/static/tax-form-3-2bb5571db7e3f6374656b1f677d6f952-dc8a5.png 250w,\n/static/tax-form-3-2bb5571db7e3f6374656b1f677d6f952-e7ffa.png 500w,\n/static/tax-form-3-2bb5571db7e3f6374656b1f677d6f952-dda24.png 750w,\n/static/tax-form-3-2bb5571db7e3f6374656b1f677d6f952-16d6d.png 933w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n</ol>\n<p>You can refer to these links for instructions on how to fill out your respective tax form: </p>\n<ul>\n<li>W-8BEN: <a href=\"https://www.irs.gov/pub/irs-pdf/iw8ben.pdf\">https://www.irs.gov/pub/irs-pdf/iw8ben.pdf</a></li>\n<li>W-9: <a href=\"https://www.irs.gov/pub/irs-pdf/iw8ben.pdf\">https://www.irs.gov/pub/irs-pdf/iw8ben.pdf</a></li>\n</ul>","frontmatter":{"title":"Payments","id":"hackers/payments","path":"/hackers/payments.html","description":null}}},"pathContext":{}}

